type PositiveTemporal =
    | { days: string }
    | { weeks: string }
    | { months: string }
    | { years: string }
    | { hours: string }
    | { minutes: string };

const toPositiveTemporal = (
    amount: string,
    unit: string,
    nonNegative?: boolean | string
): PositiveTemporal | string => {
    const isNonNegative = typeof nonNegative === 'boolean' || nonNegative === 'nonNegative';

    if (amount === '0' && isNonNegative) {
        return `amount zero in toPositiveTemporal is invalid, unit: ${unit}`;
    }

    const lowercaseUnit = unit.toLowerCase();

    switch (lowercaseUnit) {
        case 'm':
        case 'month':
            return { months: amount };
        case 'd':
        case 'day':
            return { days: amount };
        case 'w':
        case 'week':
            return { weeks: amount };
        case 'y':
        case 'year':
            return { years: amount };
        default:
            return `Invalid time unit: ${unit}`;
    }
};

export { toPositiveTemporal };
